import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
const LmsRecommendationAuthorityStaff = lazy(() =>
  import("./LmsRecommendationAuthorityStaff")
);
const LmsRecommendationAuthorityRequest = lazy(() =>
  import("./LmsRecommendationAuthorityRequest")
);
const LmsRecommendationAuthorityHistory = lazy(() =>
  import("./LmsRecommendationAuthorityHistory")
);

const LmsRecommendationAuthority = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();

  return (
    <>
      <Suspense fallback={<QvipleLoader />}>
        {searchParams.get("type") === "mod-staff" ? (
          <LmsRecommendationAuthorityStaff
            modId={getQuery?.state?.onClickValue?._id}
            instituteId={getQuery?.state?.onClickValue?.institute?._id}
          />
        ) : searchParams.get("type") === "mod-request" ? (
          <LmsRecommendationAuthorityRequest
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : searchParams.get("type") === "mod-history" ? (
          <LmsRecommendationAuthorityHistory
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : null}
      </Suspense>
    </>
  );
};

export default LmsRecommendationAuthority;
